         })
         })

         }).catch(() => {
         this.$message({
         type: 'info',
         message: ''
         });
         });*/
      },
      //
      create: function () {
        this.$http.post('/admin/user/addFirstDealer2', this.$data.query)
          .then(function (res) {
            this.$message({
              showClose: true,
              message: '',
              type: 'success'
            });
            this.$router.push('/admin/record/agentListFir')
          }, function (err) {
            this.$message({
              showClose: true,
              message: err.statusMessage,
