            });
            if(this.$route.query.level==2){
              this.$router.push('/admin/record/agentListSec')
            }else {
              this.$router.push('/admin/record/agentListFir')
            }
          }, function (err) {
            this.$message({
              showClose: true,
              message: err.statusMessage,
              type: 'error'
            });
          })
      }
    },
    filters: {
      changeName: function (val) {
        if (val == 101) {
          val = ''
        } else if (val == 102) {
          val = ''
        } else if (val == 103) {
          val = ''
        }
        return val;
      }
    }
  }
</script>

